
.aboutTitle{
    padding-left: 20%;
    padding-top: 2%;
}

.svgTitle{
    height: 20vh;
    width: 20vw;
}

#svgTitle path:nth-child(2){
    stroke-dasharray: 597px;
    stroke-dashoffset: 597px;
}

#svgTitle path:nth-child(3){
    stroke-dasharray: 554px;
    stroke-dashoffset: 554px;
}

#svgTitle path:nth-child(4){
    stroke-dasharray: 423px;
    stroke-dashoffset: 423px;
}

#svgTitle path:nth-child(5){
    stroke-dasharray: 464px;
    stroke-dashoffset: 464px;
}

#svgTitle path:nth-child(6){
    stroke-dasharray: 365px;
    stroke-dashoffset: 365px;
}

#svgTitle path:nth-child(7){
    stroke-dasharray: 873px;
    stroke-dashoffset: 873px;
}

#svgTitle path:nth-child(8){
    stroke-dasharray: 472px;
    stroke-dashoffset: 472px;
}

@keyframes line-anim {
    to {
        stroke-dashoffset: 0;
    }
}


.containerAbout{
    padding-top: 5%;
    gap: 1%;
}

.container{
    gap: 10%;
}



.cont{
    opacity: 0;
    background-color: var(--color-dark-secondary);
    border-radius: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 10vh;
}

.container1{
    height: 40vh;
}

